import React from 'react';
import { Step } from 'semantic-ui-react';

function StepPanel (props) {
  return (
    <Step.Group ordered size='large' widths={3} className='StepPanel'>
      <Step title='' />
      <Step title='' />
      <Step title='' />
    </Step.Group>
  );
}

export default StepPanel;



